import Score from '../../components/Score.vue';
export default {
	components: {
		Score
	},
	data() {
		return {
			hotImg: '../../static/icos/hot.png',
			trailerImg: '../../static/icos/interest.png',
			hobbyImg: '../../static/icos/guess-u-like.png',
			praiseImg: '../../static/icos/praise.png',
			swipeImgUrl: [],
			hotImgUrl: [],
			trailerImgUrl: [],
			hobbyImgUrl: [],
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]
		}
	},
	onLoad() {
		this.getSwiperData();
		this.getHotData();
		this.getTrailerData();
		this.getHobbyData();

		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();

	},
	onUnload() {
		// 页面卸载的时候，清除动画数据
		this.animationData = {};
		this.animationDataArr = [{}, {}, {}, {}, {}];
	},
	onPullDownRefresh() {
		this.getHobbyData();
	},
	onHide() {
		if (this.videoContext) {
			this.videoContext.pause();
		}
	},
	methods: {
		// TODO 获取轮播图数据
		getSwiperData() {
			uni.request({
				url: `${this.serverURL}/index/carousel/list`,
				method: "POST",
				header: this.header,
				data: {
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						this.swipeImgUrl = res.data.data;
					}
				}
			});
		},
		// TODO 获取热门推荐
		getHotData() {
			uni.request({
				url: `${this.serverURL}/index/movie/hot`,
				method: "POST",
				header: this.header,
				data: {
					type: 'superhero',
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						let data = res.data.data;
						this.hotImgUrl = data;
					}
				}
			});
		},
		// TODO 获取热门预告
		getTrailerData() {
			uni.request({
				url: `${this.serverURL}/index/movie/hot`,
				method: "POST",
				header: this.header,
				data: {
					type: 'trailer',
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						let data = res.data.data;
						this.trailerImgUrl = data;
					}
				}
			});
		},
		// TODO 获取猜你喜欢
		getHobbyData() {
			uni.showLoading({
				title: '加载中',
				mask: true
			});

			uni.request({
				url: `${this.serverURL}/index/guessULike`,
				method: "POST",
				header: this.header,
				data: {
					qq: this.qq
				},
				success: (res) => {
					if (res.data.status === 200) {
						let data = res.data.data;
						this.hobbyImgUrl = data;
					}
				},
				complete: () => {
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		//  TODO 点赞
		praiseClick(e) {

			let gIndex = e.currentTarget.dataset.gindex;
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 400
			});

			// 导出动画数据到view组件，实现组件的动画效果
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();

			// 还原动画
			setTimeout(() => {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			}, 500)

		},
		playVideo(id) {
			this.videoContext = uni.createVideoContext(id);
			let trailer = this.trailerImgUrl;
			for (let i = 0; i < trailer.length; i++) {
				let tempID = trailer[i].id;
				if(tempID != id) {
					uni.createVideoContext(tempID).pause();
				}
			}
		}
	}
}
